import { configureStore, createSlice } from "@reduxjs/toolkit";

let cartData = createSlice({
  name: 'cartData',
  initialState: [
    {id : 0, name : 'White and Black', count : 2},
    {id : 2, name : 'Grey Yordan', count : 1}
  ],
  reducers: {
    increaseCount(state, action){
      let target = state.find(function (x) {
        return x.id == action.payload
      })
      target.count++
    },
    concatData(state, action){
      let shoesData = action.payload
      let ids = state.map((s)=>{
        return s.id
      })
      let checkDuplication = ids.includes(shoesData.id)

      if (checkDuplication){
          let target = state.find(function (x) {
            return x.id == shoesData.id
          })
          target.count++
        }
      else{
        let newData = {
          id: shoesData.id,
          name: shoesData.title,
          count: 1
        }
        return state.concat(newData)
      }
    },
    deleteData(state, action){
      console.log(action.payload)
      return state.filter((s)=>s.id !== action.payload)
    }
  } 
})

export let {increaseCount, concatData, deleteData} = cartData.actions
export default cartData