function Shoes(props) {
  let navigate = useNavigate();
  let path = "/detail/" + props.id
  return (
    <Col md>
      <img onClick={() => { navigate(path) }} src={props.이미지경로} width="80%" />
      <h4>{props.상품명}</h4>
      <p>{props.상품가격} 원</p>
    </Col>
  )
}

